@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("/source/fonts/ptsans.woff2") format("woff2"), url("/source/fonts/ptsans.woff") format("woff");
}
@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("/source/fonts/ptsansbold.woff2") format("woff2"), url("/source/fonts/ptsansbold.woff") format("woff");
}
*,
*::after,
*::before {
  box-sizing: border-box;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

a {
  text-decoration: none;
}

img {
  width: 100%;
  height: 100%;
  display: block;
}

body {
  margin: 0;
}

ul {
  padding: 0;
  margin: 0;
  list-style-type: none;
}

.page {
  height: 100%;
  background-color: #e7e5e5;
}

.page__body {
  width: 320px;
  margin: 0 auto;
  min-height: 100%;
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 14px;
  line-height: 21px;
  font-weight: 400;
  color: #333333;
  background-color: #ffffff;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.2);
}
@media (min-width: 768px) {
  .page__body {
    width: 768px;
    margin: 0 auto;
  }
}
@media (min-width: 1200px) {
  .page__body {
    width: 1200px;
  }
}

.main-nav {
  position: relative;
}

.main-nav__wrapper {
  position: absolute;
  min-width: 320px;
  top: 0;
}
@media (min-width: 768px) {
  .main-nav__wrapper {
    background-color: #ffffff;
    min-width: 768px;
  }
}

.main-nav__toggle {
  top: 0;
  right: 0;
  border: none;
  cursor: pointer;
}
.main-nav__toggle::before, .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 27px;
  right: 20px;
  width: 27px;
  height: 4px;
  background-color: #ffffff;
  z-index: 2;
}
.main-nav__toggle::before {
  transform: rotate(45deg);
}
.main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav__toggle .main-nav__toggle:hover {
  opacity: 0.6;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}

@media (min-width: 320px) {
  .main-nav--open .main-nav__wrapper {
    display: none;
  }
}
@media (min-width: 320px) and (min-width: 768px) {
  .main-nav--open .main-nav__wrapper {
    display: block;
  }
}
@media (min-width: 320px) and (min-width: 1200px) {
  .main-nav--open .main-nav__wrapper {
    display: block;
  }
}
.main-nav--open .main-nav__toggle {
  position: absolute;
  right: 0;
  border: none;
  cursor: pointer;
}
.main-nav--open .main-nav__toggle::after {
  content: "";
  position: absolute;
  background-image: url("../img/icon-menu.svg");
  background-repeat: no-repeat;
  width: 23px;
  height: 22px;
  right: 20px;
  top: 17px;
  transform: rotate(0deg);
}
@media (min-width: 768px) {
  .main-nav--open .main-nav__toggle::after {
    display: none;
  }
}
.main-nav--open .main-nav__toggle:hover {
  opacity: 0.6;
}
.main-nav--open .main-nav__toggle:active {
  opacity: 0.3;
}

.main-nav__list {
  display: flex;
  flex-direction: column;
  background-color: #81b3d2;
}
@media (min-width: 768px) {
  .main-nav__list {
    display: grid;
    grid-template-columns: 153px 153px 153px 153px 153px;
    background-color: #ffffff;
  }
}
@media (min-width: 768px) and (min-width: 1200px) {
  .main-nav__list {
    grid-template-columns: 240px 240px 240px 240px 240px;
  }
}

.main-nav__item {
  background-color: #81b3d2;
  margin-bottom: 1px;
  box-shadow: 0 1px 0 rgba(1, 1, 1, 0.2), inset 0 1px 0 rgba(255, 255, 255, 0.2);
}
@media (min-width: 768px) {
  .main-nav__item {
    background-color: #ffffff;
    box-shadow: none;
    margin: 0;
  }
  .main-nav__item:nth-child(4n+3) {
    grid-column: 4/5;
  }
}
@media (min-width: 1200px) {
  .main-nav__item {
    padding-right: 4px;
  }
}

.main-nav__item:last-child::after {
  content: " ";
  position: absolute;
  width: 320px;
  height: 7px;
  bottom: 0;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
}
@media (min-width: 768px) {
  .main-nav__item:last-child::after {
    background: none;
  }
}

